import './less/index.less'
import 'babel-polyfill'

import {
  componentRegister,
  createApp
} from './app'

componentRegister()

const {
  app,
  router,
  store
} = createApp()

if (window.__INITIAL_STATE__) {
  store.replaceState(window.__INITIAL_STATE__)
}

router.onReady(() => {

  //  asyncData.
  //  resolve 
  // (double-fetch)
  //  `router.beforeResolve()` resolve
  console.log('router onReady =============')
  
  // 
  let loadPage = (app , store , router) => {
    const matchedComponents = router.getMatchedComponents()
    //  reject  404
    if (!matchedComponents.length) {
      return reject({
        code: 404
      })
    }

    //  `asyncData()`
    Promise.all(matchedComponents.map(Component => {
      if (Component.asyncData) {
        return Component.asyncData({
          store,
          route: router.currentRoute
        })
      }
    })).then(() => {
      // (preFetch hook) resolve 
      //  store 
      // 
      //  `template`  renderer 
      //  `window.__INITIAL_STATE__` HTML
      context.state = store.state

      resolve(app)
    }).catch(err => {

    })
  }
  loadPage(app , store , router)

  router.beforeResolve((to, from, next) => {
    const matched = router.getMatchedComponents(to)
    const prevMatched = router.getMatchedComponents(from)

    // 
    // 
    let diffed = false
    const activated = matched.filter((c, i) => {
      return diffed || (diffed = (prevMatched[i] !== c))
    })

    console.log('activated.length =============', activated.length)
    if (!activated.length) {
      return next()
    }

    // (loading indicator)
    Promise.all(activated.map(c => {
      if (c.asyncData) {
        return c.asyncData({
          store,
          route: to
        })
      }
    })).then(() => {

      // (loading indicator)

      next()
    }).catch(next)
  })

  app.$mount('#app')
})