<template>
  <div class="page-config">
    <ul class="nav">
      <li class="nav-item">
        <router-link to="/config" class="nav-link"></router-link>
      </li>
      <li class="nav-item">
        <router-link to="/invest" class="nav-link"></router-link>
      </li>
    </ul>
    <hr>
    <div class="table-responsive w-50 p-3">
      <h6>/</h6>
      <hr>
      <form @submit.prevent="investUpdateSubmit">
        <div class="form-group">
          <label for></label>
          <div>
            <input
              type="text"
              class="form-control"
              placeholder=""
              v-model="investDetail.name"
              required
            >
          </div>
        </div>

        <div class="form-group">
          <label for></label>
          <div>
            <input
              type="number"
              class="form-control"
              placeholder=""
              v-model="investDetail.num"
              required
            >
          </div>
        </div>

        <div class="form-group">
          <label for></label>
          <div>
            <input
              type="text"
              class="form-control"
              placeholder=" %"
              v-model="investDetail.rate"
              required
            >
          </div>
        </div>

        <div class="form-group">
          <label for></label>
          <div>
            <input
              type="number"
              class="form-control"
              placeholder=""
              v-model="investDetail.days"
              required
            >
          </div>
        </div>

        <div class="form-group">
          <label for></label>
          <div>
            <input
              type="number"
              class="form-control"
              placeholder=""
              v-model="investDetail.sort"
              required
            >
          </div>
        </div>

        <div class="form-group">
          <hr>
          <input type="hidden" v-model="investDetail.id">
          <input type="submit" class="btn btn-primary pl-3 pr-3" value="">
          <input type="reset" class="btn btn-warning pl-3 pr-3" value="">
        </div>
      </form>
    </div>
